/**
 * Example of a custom Image selector
 * Key is to emit a select-file event when a file needs to be added
 */
import { VImg } from 'vuetify/lib'
export default {
  name: "ImagenURL",
  components: { VImg },
  data() {
      // Some public domain images from wikimedia.
      return {
          imageUrl: '',
          imageAlt: '',
      };
  },
  methods: {
    selectImage() {
      this.$emit('select-file', {
          src: this.imageUrl,
          alt: this.imageAlt,
      });
    }
  }
}
